import { combineReducers } from 'redux';
import CartReducer from './CartReducer';
import RollingDiceReducer from './RollingDiceReducer';
import BookingTicketReducer from './BookingTicketReducer';


const rootReducer = combineReducers({ // store tng ca ng dng
    stateCart: CartReducer,         // state gi hng
    RollingDiceReducer,              // state bai rolling dice, kiu vit nng cp khng cn khai bo
    BookingTicketReducer            // state bai Booking movie ticket
})

export default rootReducer;