
const stateDefault = {
    seatList: [
        // { "soGhe": "A1", "gia": 1000 },
        // { "soGhe": "B2", "gia": 1000 },
        // { "soGhe": "C3", "gia": 1000 }
    ]
}

const BookingTicketReducer = (state = stateDefault, action) => {
    switch (action.type) {
        case 'BOOK_SEAT': {
            let tempSeatList = [...state.seatList];
            let index = tempSeatList.findIndex(item => item.soGhe === action.seat.soGhe)
            if (index !== -1) {
                // khi ngi dng click v chn, nu tn ti sn trong mng (chn ri) th remove ra khi mng
                tempSeatList.splice(index, 1);
            } else {
                // nu cha c th chn, add v mng
                tempSeatList.push(action.seat)
            }

            // update state or setState, render li giao din
            state.seatList = tempSeatList;
            return { ...state }
        }

        case 'REMOVE_SEAT': {

            let tempSeatList = [...state.seatList];
            let index = tempSeatList.findIndex(item => item.soGhe === action.seat.soGhe)
            tempSeatList.splice(index, 1)

            state.seatList = tempSeatList;
            return { ...state }
        }
        default: return { ...state }
    }
}

export default BookingTicketReducer